.Overlay {
  position: absolute;
  text-transform: capitalize;
  font-family: sans-serif;
  font-size: x-small;  
  top: 20px;
  right: 0px;
  background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);  padding: 15px; 
  border-top-left-radius: 5px; 
  border-bottom-left-radius: 5px;
  /*border-style: groove;*/
}

.Overlay img {
  width: 350px;
  /*height: 150px;*/

}

.Overlay button {
	position: absolute;
  border-color: transparent;
	border-style: solid;
  top:30px;
  right: 10px;
  font-weight: normal;
  font-size: small;

}


